// stats - массив объектов содержащих информацию о элементе статистики. 
// Может иметь произвольное кол - во элементов.
// Цвет фона элемента статистики в оформлении можно пропустить, либо создать 
// функцию для генерации случайного цвета.

import PropTypes from 'prop-types';
import styles from '../Statistics/Statistics.module.css';


export function Statistics({title, stats}) {
  return <section className={styles.statistics}>
           {title && <h2 className={styles.title}>Upload stats</h2>}
         
          <ul className={styles.statList}>
            {stats.map(data => {
              return <li className={styles.item} key={data.id}>
                <span className={styles.label}>{data.label}</span>
                <span className={styles.percentage}>{data.percentage}%</span>
             </li>})}
           </ul>
         </section>
}


